const Projects = (props) => {
    // const [device, setDevice] = useState(true);
    // const [orientation, setOrientation] = useState(true);
    // const [showDescription, setShowDescrition] = useState(true);
    const history = useHistory();
    const { path, url } = useRouteMatch();
    // const toggleDevice = (device) => {
    //     console.log(device);
    //     switch (device) {
    //         case "Mobile":
    //             setDevice(true);
    //             break;
    //         case "Desktop":
    //             setDevice(false);
    //             break;
    //         case "L":
    //             setOrientation(true);
    //             break;
    //         case "P":
    //             setOrientation(false);
    //             break;
    //         default:
    //             setDevice(true);
    //             break;
    //     }
    // };
    const toggleContent = (isContent, project) => {
        // setShowDescrition(isContent);
        console.log(project);
        console.log(`${url}/${project}`);
        switch (project) {
            case "poker":
                history.push(`${url}/poker`);
                break;
            case "insideOut":
                history.push(`${url}/insideout`);
                break;
            case "ecom":
                history.push(`${url}/ecommerce`);
                break;
            case "roulette":
                history.push(`${url}/roulette`);
                break;
            default:
                break;
        }
    };

    return (
        <div className="Projects">
            <NavPanel toggleContent={toggleContent}></NavPanel>
            {/* {!showDescription && <DeviceSelect toggleDevice={toggleDevice} />} */}

            <div className="projectDescription">
                write the project page into content here
                <Switch>
                    <Route path={`${path}/poker`}>
                        <Poker></Poker>
                    </Route>
                    <Route path={`${path}/ecommerce`}>
                        <Ecom></Ecom>
                    </Route>
                    <Route path={`${path}/roulette`}>
                        <Roulette></Roulette>
                    </Route>
                    <Route path={`${path}/insideout`}>
                        <InsideOut></InsideOut>
                    </Route>
                </Switch>
            </div>
        </div>
    );
};

export default Projects;
